$primaryColor: #4527a4;

// Common CSS
html {
	scroll-behavior: smooth;
}

// Dashicon
.dashicons {
	font-family: dashicons !important;
}

// Social Sharing
.wp-block-bssb-social-share {
	* {
		box-sizing: border-box;
	}

	ul.bssbSocialShare {
		list-style-type: none;
		line-height: 1;
		margin: 0;
		padding: 5px;
		display: inline-flex;
		align-items: center;
		flex-wrap: wrap;

		li {
			color: #4e4e4e;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 50%;
			cursor: pointer;

			&:first-child {
				margin-left: 0 !important;
			}

			&:last-child {
				margin-right: 0 !important;
			}

			i {
				font-style: inherit;
			}

			img {
				max-width: unset;
			}
		}
	}
}